<template>
  <div>
    <h2 class="grey--text mb-1">Tambah Materi</h2>
    <v-form @submit.prevent="">
      <!-- <p v-text="mapel"></p> -->
      <div class="d-flex" v-if="!newBab">
        <v-select
          label="Pilih BAB"
          :items="listBab"
          v-model="namaBab"
        ></v-select>
        <b class="align-self-center text-h5">/</b>
        <v-btn
          @click="
            () => {
              newBab = true;
              namaBab = '';
            }
          "
          text
          class="align-self-center"
          color="primary"
        >
          <v-icon>mdi-plus</v-icon>
          Baru
        </v-btn>
      </div>
      <div class="d-flex" v-if="newBab">
        <v-text-field label="Nama BAB" v-model="namaBab"></v-text-field>
        <b class="align-self-center text-h5">/</b>
        <v-btn
          @click="newBab = false"
          text
          class="align-self-center"
          color="primary"
        >
          <v-icon>mdi-refresh</v-icon>
          Pilih BAB
        </v-btn>
      </div>

      <div class="d-flex" v-if="!newMateri && !!namaBab && !newBab">
        <v-select
          label="Pilih materi"
          :items="listMateri"
          v-model="namaMateri"
        ></v-select>
        <b class="align-self-center text-h5">/</b>
        <v-btn
          @click="
            () => {
              newMateri = true;
              namaMateri = '';
            }
          "
          text
          class="align-self-center"
          color="primary"
        >
          <v-icon>mdi-plus</v-icon>
          Baru
        </v-btn>
      </div>
      <div class="d-flex" v-if="newMateri && namaBab">
        <v-text-field label="Nama Materi" v-model="namaMateri"></v-text-field>
        <b class="align-self-center text-h5">/</b>
        <v-btn
          @click="newMateri = false"
          text
          class="align-self-center"
          color="primary"
        >
          <v-icon>mdi-refresh</v-icon>
          Pilih materi
        </v-btn>
      </div>
      <div v-show="!newMateri && namaMateri && !newBab && namaBab">
        <v-divider class="primary"></v-divider>
        <v-text-field
          label="Nama sub materi"
          v-model="form.title"
        ></v-text-field>
        <p>Silahkan Tulis materi anda dibawah ini!</p>
        <vue-editor v-model="form.content"></vue-editor>
      </div>
      <v-btn @click="submit" color="primary" class="mt-5" block>Submit</v-btn>
    </v-form>
  </div>
</template>

<script>
import { onMounted, watch, reactive, ref } from "@vue/composition-api";
export default {
  name: "materiTambah",
  props: ["user"],
  setup(props, { root }) {
    const newMateri = ref(false);
    const newBab = ref(false);
    const listBab = ref([]);
    const listMateri = ref([]);
    const mapel = ref({});
    const namaBab = ref("");
    const namaMateri = ref("");
    const idBab = ref("");
    const idMateri = ref("");
    const form = reactive({
      title: "",
      content: "",
    });
    watch(namaBab, (val) => {
      if (!newBab.value) {
        const bab = mapel.value.bab.filter((bab) => bab.babTitle === val)[0];
        idBab.value = bab._id;
        listMateri.value = bab.materi.map((val) => val.materiTitle);
      }
    });
    watch(namaMateri, (val) => {
      if (!newMateri.value) {
        const bab = mapel.value.bab.filter((bab) => bab._id === idBab.value)[0];
        const materi = bab.materi.filter(
          (materi) => materi.materiTitle === val
        )[0];
        idMateri.value = materi._id;
      }
    });

    const getData = () => {
      root.bus.$emit("loadingStart");
      root.axios
        .get("user/pelajaran/mapel/data/" + encodeURI(props.user.username))
        .then(({ data }) => {
          mapel.value = data.mapel;
          listBab.value = mapel.value.bab.map((val) => val.babTitle);
        })
        .catch((err) => err.response)
        .then(() => root.bus.$emit("loadingStop"));
    };
    onMounted(() => {
      getData();
    });
    const submit = () => {
      root.bus.$emit("startLoading");
      if (newBab.value && !newMateri.value) {
        //BAB BARU
        const payload = {
          _id: mapel.value._id,
          babTitle: namaBab.value,
        };
        root.axios
          .post("user/pelajaran/mapel/bab", payload)
          .then((res) => {
            root.bus.$emit("showSnackbar", {
              message: res.data.message,
              show: true,
            });
            root.$router.push("/");
          })
          .catch((err) => console.log(err))
          .then(() => root.bus.$emit("stopLoading"));
      } else if (!newBab.value && newMateri.value) {
        //MATERI BARU
        const payload = {
          idMapel: mapel.value._id,
          idBab: idBab.value,
          materiTitle: namaMateri.value,
        };
        root.axios
          .post("user/pelajaran/mapel/materi", payload)
          .then((res) => {
            root.bus.$emit("showSnackbar", {
              message: res.data.message,
              show: true,
            });
            root.$router.push("/");
          })
          .catch((err) => console.log(err))
          .then(() => root.bus.$emit("stopLoading"));
      } else if (!newBab.value && !newMateri.value) {
        //SUBMATERI BARU
        const payload = {
          idMapel: mapel.value._id,
          idBab: idBab.value,
          idMateri: idMateri.value,
          type: "materi",
          ...form,
        };
        root.axios
          .post("user/pelajaran/mapel/submateri", payload)
          .then((res) => {
            root.bus.$emit("showSnackbar", {
              message: res.data.message,
              show: true,
            });
            root.$router.push("/");
          })
          .catch((err) => console.log(err))
          .then(() => root.bus.$emit("stopLoading"));
      }
    };
    return {
      form,
      newMateri,
      listBab,
      listMateri,
      mapel,
      newBab,
      namaBab,
      namaMateri,
      submit,
    };
  },
};
</script>

